<template>
    <el-row>
        <el-col :span="4">
            <el-card class="box-card left-aside">
                <div slot="header" class="">
                    <span>角色</span>
                </div>
                <div style="height: 70vh">
                    <el-scrollbar style="height: 100%">
                        <div
                            v-for="(item, index) in roles"
                            :key="item.ROLEID"
                            :ref="`role${item.ROLEID}`"
                            :class="item.ROLEID == selectRole ? 'active' : ''"
                            class="left-item"
                            @click="changeRole(item.ROLEID)"
                        >
                            <div>
                                <i class="el-icon-price-tag"></i>
                                {{ item.rolename }}
                            </div>
                        </div>
                    </el-scrollbar>
                </div>
            </el-card>
        </el-col>
        <el-col :span="20" style="padding: 0 20px">
            <el-card class="box-card">
                <div slot="header" class="">
                    <span>菜单</span>
                    <el-button
                        :loading="loadingSave"
                        @click="handleSave"
                        style="float: right; padding: 3px 0"
                        type="text"
                        >保存</el-button
                    >
                </div>
                <div class="block">
                    <el-row type="flex" justify="center" class="tree-parent">
                        <el-col v-for="item in rolerights" :key="item.MENUID">
                            <el-tree
                                :data="item"
                                ref="roleRightTree"
                                show-checkbox
                                default-expand-all
                                node-key="id"
                                :default-checked-keys="checkedMenuids"
                                :props="defaultProps"
                            >
                            </el-tree>
                        </el-col>
                    </el-row>
                </div>
            </el-card>
        </el-col>
    </el-row>
</template>

<script>
import {
    getCtRoleRightAll,
    saveCtRoleRight,
    getCtRoleAll,
} from "../../api/api";

export default {
    components: {},
    data() {
        return {
            selectRole: 0,
            loadingSave: false,
            roles: [],
            rolerights: [],
            checkedMenuids: [],
            defaultProps: {
                children: "children",
                label: "label",
            },
        };
    },
    mounted() {
        this.getRoles();
    },
    methods: {
        getRoles() {
            getCtRoleAll().then((res) => {
                if (res.success) {
                    let data = res.data;
                    // let data = res.data.filter((item) => {
                    //     return item.ROLEID > 1;
                    // });
                    this.roles = data;
                    if (data.length > 0) {
                        if (this.selectRole == 0) {
                            this.selectRole = data[0]["ROLEID"];
                        }

                        this.getRoleRights();
                    }
                }
            });
        },
        getRoleRights() {
            let param = { ROLEID: this.selectRole };
            getCtRoleRightAll(param).then((res) => {
                if (res.success) {
                    // let data = res.data.filter((item) => {
                    //     return item.ROLEID > 1;
                    // });
                    let rolerightall = [];
                    let item1 = {};
                    let item2 = {};
                    this.checkedMenuids = [];
                    res.data.forEach((item) => {
                        if (item.MENUID.slice(-3) == "000") {
                            //第一级
                            if (Object.keys(item2).length > 0) {
                                item1.children.push(item2);
                            }
                            item2 = {};

                            if (Object.keys(item1).length > 0) {
                                rolerightall.push([item1]);
                            }
                            item1 = {
                                id: item.MENUID,
                                label: item.name,
                                children: [],
                            };
                        } else if (item.sort.slice(-2) == "00") {
                            //第二级
                            if (Object.keys(item2).length > 0) {
                                item1.children.push(item2);
                            }
                            item2 = {
                                id: item.MENUID,
                                label: item.name,
                                children: [],
                            };
                            if (item.brole == "1") {
                                this.checkedMenuids.push(item.MENUID);
                            }
                        } else {
                            //第三级
                            item2.children.push({
                                id: item.MENUID,
                                label: item.name,
                            });
                            if (item.brole == "1") {
                                this.checkedMenuids.push(item.MENUID);
                            }
                        }
                    });
                    //收尾
                    if (Object.keys(item2).length > 0) {
                        item1.children.push(item2);
                    }
                    if (Object.keys(item1).length > 0) {
                        rolerightall.push([item1]);
                    }
                    this.rolerights = rolerightall;
                }
            });
        },
        handleSave() {
            this.loadingSave = true;
            let MENUIDS = [];
            this.$refs.roleRightTree.forEach((t) => {
                let tempids = t.getCheckedKeys(true);
                MENUIDS = MENUIDS.concat(tempids);
            });
            let para = {
                ROLEID: this.selectRole,
                MENUIDS: MENUIDS.join(","),
            };
            saveCtRoleRight(para).then((res) => {
                if (res.success) {
                    this.$message({
                        message: "保存成功",
                        type: "success",
                    });
                    this.getRoles();
                } else {
                    this.$message({
                        message: res.msg,
                        type: "error",
                    });
                }

                this.loadingSave = false;
            });
        },
        changeRole(val) {
            this.selectRole = val;
            this.getRoleRights();
        },
    },
};
</script>
<style  scoped>
.box-card /deep/ .el-scrollbar__wrap {
    overflow-x: hidden;
}
.tree-parent /deep/ .el-col:not(:last-child) {
    border-right: 1px solid #f1f1f1;
}
.tree-parent /deep/ .el-tree-node .el-tree-node .el-tree-node {
    float: left;
}
</style>


